export const logoVariants = {
    start: {
      pathLength: 0,
      fill: "rgba(229, 16, 19, 0)",
      opacity: 1
    },
    end: {
      pathLength: 1,
      fill: "rgba(229, 16, 19, 1)",
      transition: {
        default: { delay: 0.5, duration: 3, ease: [1, 0, 0.8, 1] }, //      
        fill: { delay: 0.5, duration: 3, ease: [1, 0, 0.8, 1] }, //      
      },
    },
    hover: {
      scale: 0.9,
      transition: {
        yoyo: Infinity,
      },
    },
  };
  
  export const logoAnimationSequence = async (
    logoControls: any,
    boxControls: any,
    setIsLoginVisible: (value: boolean) => void
  ): Promise<void> => {
    // 1.    
    await boxControls.start({
      opacity: 0,
      scale: 0.8,
      transition: { duration: 0.2 }, //    
    });
  
    // 2.    
    setIsLoginVisible(false);
  
    // 3.    
    await logoControls.start({
      opacity: 1,
      x: "-50%",
      y: "-50%",
      scale: 1.5, //   
      transition: { duration: 3 }, //    
    });
  
    // 5.    (  )
    await logoControls.start("end");
  };
  