// src/views/home-main/Banner.tsx
import React, { useEffect, useState, useRef } from 'react';
import ReactPlayer from 'react-player';
import URLService from '../../util/movie/URL';
import { setCache, getCache } from '../../util/cache/movieCache'; //   
import i18n from '../../locales/i18n';
import './Banner.css';

interface BannerProps {
  movie: {
    id: number;
    title: string;
    overview: string;
    backdrop_path: string;
    teaserKey?: string;
  } | null;
}

const Banner: React.FC<BannerProps> = ({ movie }) => {
  const [teaserKey, setTeaserKey] = useState<string | null>(null); // teaser key 
  const [showImage, setShowImage] = useState(true); //   
  const [isPlaying, setIsPlaying] = useState(false); //   
  const [timeLeft, setTimeLeft] = useState(5); //    (3)
  const playerRef = useRef<ReactPlayer>(null);
  const urlService = new URLService();

  useEffect(() => {
    const fetchTeaser = async () => {
      if (movie) {
        const cacheKey = `teaserKey_${i18n.language}_${movie.id}`; // movie.id    
        const cachedTeaserKey = getCache(cacheKey);
        const apiKey = process.env.REACT_APP_TMDB_API_KEY;

        if (cachedTeaserKey) {
          //     
          console.log(`Using cached teaserKey for movie ${movie.id}:`, cachedTeaserKey);
          setTeaserKey(cachedTeaserKey);
          return;
        }

        try {
          const videos = await urlService.getVideos(apiKey, movie.id);
          // console.log('Fetched Videos:', videos); //   
          const teaser = videos.find((video: any) => video.type === 'Trailer' && video.site.toLowerCase() === 'youtube');
          if (teaser) {
            setTeaserKey(teaser.key);
            setCache(cacheKey, teaser.key, 3600000); // 1  
            console.log(`Set cached teaserKey for movie ${movie.id}:`, cachedTeaserKey);

          }
        } catch (error) {
          console.error('Error fetching teaser video:', error);
        }
      }
    };
  
    fetchTeaser();
  }, [movie, urlService]);


  useEffect(() => {
    if (teaserKey && timeLeft > 0) {
      const timer = setTimeout(() => {
        setTimeLeft((prev) => prev - 1); // 1 
      }, 1000);

      return () => clearTimeout(timer); // 
    }

    if (timeLeft === 0) {
      setShowImage(false);
      setIsPlaying(true);
    }
  }, [teaserKey, timeLeft]);


  if (!movie) return null;
  const backdropUrl = `https://image.tmdb.org/t/p/original${movie.backdrop_path}`;



  return (
    <div className="banner">
      <div className={`banner-image ${!showImage ? 'hidden' : ''}`} style={{ backgroundImage: `url(${backdropUrl})` }}>
        <div className="banner-content">
          <h1>{movie.title}</h1>
          <p>{movie.overview}</p>
          <button className="play-btn title-btn" onClick={()=>{setIsPlaying(true);setShowImage(false);}}>
              <div
                className="progress-bar"
                style={{
                  width: `${((5 - timeLeft) / 5) * 100}%`, //    
                }}
              ></div>
              <span >
                {timeLeft > 0 ? ` ${timeLeft} ` : ' ...'}
              </span>
            </button>
          <button className="info-btn title-btn"> </button>

        </div>
      </div>
      <div className={`video-wrapper ${!showImage ? 'visible' : ''}`}>
        <ReactPlayer
          ref={playerRef}
          url={`https://www.youtube.com/watch?v=${teaserKey}`}
          playing={isPlaying}
          // controls
          volume={1}
          muted={true} //  
          className="react-player"
          width="100%"
          height="100%"
          loop={true}
          stopOnUnmount={true}
          style={{objectFit:"cover"}}
        />
      </div>
    </div>
  );
};

export default Banner;
